<template>
  <div style="min-height: 40em" >
    <div class="list-arc-item left" v-for="vol in list">
      <a href="javascript:void (0)" @click="jumplink(vol)" target="_self" :title="vol.title">
        <img v-lazy="imageSrc(vol.litpic)" :alt="vol.title" :title="vol.title" >

        <div class="list-box">
          <h2>{{vol.title}}</h2>
          <div class="info">{{vol.description}}</div>
        </div>
      </a>
      <div class="tags">

        <i class="fa fa-clock-o" data-toggle="tooltip" :title="'时间：'+vol.create_time" > {{vol.create_time}}</i>
        <i class="fa fa-comments" data-toggle="tooltip" title="评论：0"> 0</i>
        <i class="fa fa-thumbs-o-up hidden-xs" data-toggle="tooltip" :title="'点赞：'+vol.click"> {{vol.click}}</i>
        <i class="fa fa-eye" data-toggle="tooltip" :title="'浏览：'+vol['views']"> {{vol['views']}}</i>
      </div>
      <div v-if="vol.flag === 'h'" class="flag bg-red">头条</div>
    </div>

  </div>
</template>

<script>

  export default {
    computed:{
      list(){
        return this.$store.state.front.archive
      }
    },
    watch:{
      '$route': 'fetchList'
    },
    updated(){
      $("[data-toggle='tooltip']").tooltip();
    },
    methods: {
      imageSrc(image_name) {
        if (image_name){
          return '/api/download/' + image_name
        }
      },
      jumplink(article) {
        this.$store.commit('front/SET_TYPE_ID', article.type_id)
        this.$router.push({
          name: 'detail_page',
          params: {id: article.id}
        })
      },
      fetchList() {
        this.$store.dispatch('front/fetchArchive')
      }
    },
    created(){
      this.fetchList()
    }
  }
</script>




